import React from 'react';
import ReactDOM from 'react-dom';

import FoodFinderAppPics from '../images/FoodFinderAppPics.png'
import ttt from '../images/TicTacToe.PNG'
import guat from '../images/GuatPic.PNG'

import { makeStyles } from '@material-ui/core/styles';
import {Button} from "@material-ui/core";
import {SocialIconCustom} from "./Footer";

import ComputerIcon from '@material-ui/icons/Computer';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PhoneAndroidIcon from '@material-ui/icons/PhoneAndroid';
import ExploreIcon from '@material-ui/icons/Explore';
import VideogameAssetIcon from '@material-ui/icons/VideogameAsset';


//convoluted crap to import all images
function importAll(r) {
  return r.keys().map(r);
}

const images = importAll(require.context('../images/', false, /\.(png|jpe?g|svg)$/));


const SquareDisplay = (props)=>{
  const fontSize=30;
  const styles = useStyles({...props});
  console.log('props', props)
  
  const hasPicture = ()=>{
    let src = 'data:image/jpeg;base64,'; //TODO replace base image
    if (props.imgSrc){
      switch (props.imgName){
        case "FoodFinder":
          src = FoodFinderAppPics;
          break;
        case "ttt":
          src = ttt;
          break;
        case 'vball':
          src= props.imgSrc;
          break;
      }
      console.log('src', src)

    }
    return(
      <div className={styles.imageHolder}>
        <img className={styles.image} src={src}/>
      </div>
    )
  }

  const hasRepo = ()=>{
    if (props.ghSource){
      
      return (
          <SocialIconCustom bgColor={'#fcfcfc'} bgBase={'#fcfcfc00'} url={props.ghSource}/>
      )
    }
  }

  const makeIcon = ()=>{
    switch (props.icon) {
      case 'web': return(<ComputerIcon style={{ fontSize: fontSize }}/>);
      case 'cart': return(<ShoppingCartIcon style={{ flex:1, fontSize: fontSize }}/>);
      case 'phone': return(<PhoneAndroidIcon style={{ fontSize: fontSize }}/>);
      case 'assess': return(<ExploreIcon style={{ fontSize: fontSize }}/>);
      case 'vg': return(<VideogameAssetIcon style={{ fontSize: fontSize }}/>);
    }
  }


  return(
      <a className={styles.linkStyle} href={props.link || ''} target={'_blank'}>
        <div className={styles.base}>
          {hasPicture()}
          <h2>{props.title}</h2>
          <p>{props.description}</p>
          <div className={styles.iconHolder}>
            {makeIcon()}
            {hasRepo()}
          </div>
        </div>
      </a>
  )
}

const useStyles = makeStyles({
  iconHolder:{
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-around",
  },

  linkStyle:{
    textDecoration: "none",
    color: "inherit",
    cursor: "unset",

    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
  },

  base:{
    width:'90%',
    height:'600px',
    // margin: '10px',
    // maxHeight:'400px',
    
    border:"solid",
    borderRadius: '2%',
    padding: '1%',
    alignSelf: "center",
  
    display:"flex",
    flexDirection:"column",
    backgroundColor: props => props.bgColor || 'rgb(255,180,25)'
  },
  imageHolder:{
    flex: 3,

    display: "flex",
    width: '100%',
    height:'50%',
    alignSelf: "center",
    objectFit: "contain",
    
    borderBottom: '3pt solid black',
    borderRadius: '2%'
    
  },
  image:{
    // position: "absolute",
    width: '100%',
    height:'100%',
    borderRadius: '2%'
  },
  
  smallItem:{
    display:"flex",
    alignSelf: "center",
  },
  iconBG:{
  backgroundColor:'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  },
  Icon:{
    height:'25px',
    width:"auto",
  },
  
});


export default SquareDisplay;